@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
  color: #0E0E0E;
  min-width: 320px;
  background-color: #101c2d;
  font-family: 'Roboto', sans-serif; }

html {
  box-sizing: border-box;
  overflow-y: scroll;
  /* margin-left: calc(100vw - 100%);
  margin-right: 0; */ }

*,
*::before,
*::after {
  box-sizing: inherit; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

p,
ul,
li {
  margin: 0;
  padding: 0; }

.container {
  max-width: 1255px;
  margin: 0 auto;
  padding: 0 15px 0 15px; }

.header {
  width: 100%;
  height: 115px;
  background-color: rgba(16, 28, 45, 0.5);
  transition: 0.5s; }
  .header:hover {
    background-color: #101c2d;
    box-shadow: 0px 10px 29px -3px #000000; }

.nav {
  display: flex;
  justify-content: space-between;
  padding: 17px 0 17px 0; }

.nav ul {
  display: flex;
  align-items: center;
  padding-top: 15px; }

.nav__list-logo li {
  list-style: none; }

.nav__list-nav li {
  list-style: none;
  margin: 0 0 0 30px; }

.nav__list-cabinet li {
  list-style: none;
  margin: 0 0 0 30px; }

.nav a {
  text-decoration: none;
  font-size: 16px;
  line-height: 19px;
  color: #7BB5D2; }

.wrapper {
  display: flex; }

.burger {
  display: none;
  color: #fff; }

.cabinet-btn {
  position: relative;
  width: 193px;
  height: 43px;
  padding: 0;
  border: none;
  color: #fff;
  background: linear-gradient(90deg, #4481EB 0%, #04BEFE 100%);
  border-radius: 33px; }

.cabinet-btn:before {
  content: 'Личный кабинет';
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 193px;
  height: 43px;
  border: none;
  color: #fff;
  background: linear-gradient(90deg, #04BEFE 0%, #4481EB 100%);
  border-radius: 33px;
  opacity: 0;
  transition: 0.5s; }

.cabinet-btn:hover:before {
  opacity: 1; }

@media (max-width: 976px) {
  .nav ul {
    display: block;
    text-align: center; }
  .wrapper {
    position: absolute;
    display: block;
    background-color: #222220;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    position: fixed; }
  .burger {
    display: block; } }

.content {
  display: flex;
  justify-content: space-between; }

.content__cards {
  width: 69%; }

.content__stat {
  width: 29%; }

.content__cards-item {
  width: 100%;
  height: 445px;
  margin: 0 0 20px 0;
  background: #101C2D;
  box-shadow: 0px 12px 44px rgba(0, 0, 0, 0.15);
  border-radius: 12px;
  display: flex; }

.content__stat-item {
  width: 100%;
  height: 112px;
  background-color: aqua;
  margin: 0 0 20px 0; }

.content__cards-img {
  display: block;
  margin-left: -24px;
  margin-top: -16px; }

.slider {
  position: relative;
  overflow: hidden; }

.slider__wrapper {
  display: flex;
  transition: transform 0.6s ease; }

.slider__item {
  flex: 0 0 100%;
  max-width: 100%; }

.slider__control {
  position: absolute;
  top: 50%;
  display: none;
  align-items: center;
  justify-content: center;
  width: 40px;
  color: #fff;
  text-align: center;
  opacity: 0.5;
  height: 50px;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5); }

.slider__control_show {
  display: flex; }

.slider__control:hover,
.slider__control:focus {
  color: #fff;
  text-decoration: none;
  outline: 0;
  opacity: .9; }

.slider__control_left {
  left: 0; }

.slider__control_right {
  right: 0; }

.slider__control::before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  background: transparent no-repeat center center;
  background-size: 100% 100%; }

.slider__control_left::before {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E"); }

.slider__control_right::before {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E"); }

.slider__item > div {
  line-height: 250px;
  font-size: 100px;
  text-align: center; }

.slider__item-img {
  width: 100%; }

.burger {
  width: 14px;
  height: 3px;
  background-color: #fff;
  position: relative;
  top: -10px; }

.burger::before {
  position: absolute;
  content: '';
  top: 10px;
  left: -8px;
  z-index: 1;
  width: 22px;
  height: 3px;
  background-color: #fff; }

.burger::after {
  position: absolute;
  content: '';
  top: -10px;
  left: -8px;
  z-index: 1;
  width: 22px;
  height: 3px;
  background-color: #fff; }
